/*@import url('https://fontlibrary.org/face/charis');*/
:root {
  --doc-font-family: serif;
  --doc-font-size: 9pt;
  --doc-math-font-size: .9em;
  --doc-math-font-weight: regular;

  --ui-font-family: 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Ubuntu', 
    'Source Sans Pro', 'Open Sans', sans-serif;
  --ui-font-family-monospace: "Inconsolata", "Consolas", 
    "Menlo", "Ubuntu Mono", "Roboto Mono", "Courier", monospace;
  --ui-font-size: 3mm;

  --edit-color-dark: rgba(0,150,100,1);
  --edit-color-light: rgba(50,200,150,0.7);
  --edit-color-faded: rgba(100,250,150,0.01);
}
@page { margin: 0 }
body { margin: 0 }


/** For screen preview **/
@media screen {
  html, body { background-color: #aaa; }
  .page-outer {
    background: white;
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.3);
    margin: 3mm auto;
  }
  .page-overflowing {
    outline: 4px dotted red;
  }
}

@media print {
  .document {
    position: absolute;
  }
  .page-outer {
    page-break-inside: avoid;
    page-break-after: always;
    overflow: hidden;
  }
}
.katex-html {
  white-space: nowrap;
  font-size: 0.9em;
  font-size: var(--doc-math-font-size, 0.9em);
  font-weight: normal;
  font-weight: var(--doc-math-font-weight, normal);
}


.document blockquote {
  font-style: italic;
}

.document em > strong {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

/* TABLES */

.document table {
  border-collapse: collapse;
}

.document th {
  font-weight: bold;
}

.document th, .document td {
  border: 1px solid black;
  padding: 0.2em 0.5em;
}

.document {
  font-family: serif;
  font-family: var(--doc-font-family, serif);
  font-size: 9pt;
  font-size: var(--doc-font-size, 9pt);
  counter-reset: question 0;
  counter-reset: question var(--doc-startnumber, 0);
  display: flex;
  flex-direction: column;
}

.answerchoice-list {
  list-style: none;
  padding: 0;
}

.answerchoice-item {
  margin: 2mm;
}

.answerchoice-item:before {
  counter-increment: answerchoice 1;
  content: counter(answerchoice, lower-alpha) ') ';
  margin-right: 2mm;
}

.answerkey {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.1em;
  counter-reset: answerkey 0;
  counter-reset: answerkey var(--doc-startnumber, 0);
  margin: 0;
  padding: 0;
  position: relative;
  max-height: 90%;
}

.answerkey-item {
  margin: 1mm;
}

.answerkey-item::before {
  counter-increment: answerkey 1;
  content: counter(answerkey) '. ';
}

.blank-underline {
  display: inline-block;
  border-bottom: 1px solid black; 
  margin: 0 1mm;
}

/* .blank-underline-heading */

.editable > *:hover {
  outline: 3px dotted lightgreen;
  outline: 3px dotted var(--edit-color-light, lightgreen);
  background-color: var(--edit-color-faded);
}

.header {
  border-collapse: collapse;
  width: 100%;
}

.header td {
  border: none;
  font-size: 1.4em;
  font-weight: bold;
  border: none;
  padding-bottom: 3mm;
  text-align: left;
}

.header td:last-child {
  text-align: right;
  right: 0;
}

.header-empty {
  height: 5mm;
}

.list-auto {
  list-style-type: upper-roman;
}

.question-wrapper {
  display: flex;
  margin: 1em 0;
}

.question-wrapper::before {
  counter-increment: question 1;
  counter-reset: answerchoice 0;
  content: counter(question) '. ';
  font-size: 1em;
  position: relative;
}

:root {
  --layout-answer-diagram: 'answer diagram'
}

.question-layout {
  display: grid;
  padding: 0;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
  width: 100%;
}

.question-layout {
  grid-template-areas: 
    'instructions instructions'
    'question question'
    'answer diagram';
  grid-template-areas: 
    'instructions instructions'
    'question question'
    var(--layout-answer-diagram);
}

.question-wrapper > .question-layout {
  margin-left: 1em;
}

.question-area {
  margin-bottom: 0.5em;
  margin-right: 0.5em;
}

body {
  font-family: sans-serif;
  font-family: var(--ui-font-family, sans-serif);
}

button, input, select, textarea {
  border-radius: 4px;
  border-color: #ccc;
  border-width: 1px;
  border-style: solid;
  background-color: white;
  color: black;
  font-family: sans-serif;
  font-family: var(--ui-font-family, sans-serif);
  margin: 1px;
  font-size: 0.9rem;
}

input {
  padding-left: 5px;
}

textarea {
  font-family: monospace;
  font-family: var(--ui-font-family-monospace, monospace);
  font-size: 1.1em;
}

button:hover, input:hover, select:hover, textarea:hover {
  color: black;
  border-color: #999;
}

button:focus, input:focus, select:focus, textarea:focus {
  outline: none;
}

button:active, input:active, select:active, textarea:active {
  outline: none;
}

button {
  background-image: -webkit-linear-gradient(white, 50%, #f5f5f5);
  background-image: linear-gradient(white, 50%, #f5f5f5);
}

button:hover {
  background-image: -webkit-linear-gradient(white, 50%, #eee);
  background-image: linear-gradient(white, 50%, #eee);
}

button:active {
  color: white;
  border-color: black;
  background-image: -webkit-linear-gradient(bottom, #27f, 50%, #05f);
  background-image: linear-gradient(0deg, #27f, 50%, #05f);
}

pre, code {
  font-family: monospace;
  font-family: var(--ui-font-family-monospace, monospace);
}




html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow: hidden;
  font-size: 10pt;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
